body {
    margin: 0;
    padding: 0;
    overflow: hidden;
    font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
}

.map {
    width: 100%;
    height: 100%;
}

.monitor__marker {
    opacity: 0;
    -webkit-animation: popupAndHide 2.5s linear 1;
            animation: popupAndHide 2.5s linear 1;
}

.eventTracker {
    left: 0px;
    bottom: 0px;
    position: fixed;
    width: 100%;
    height: 30%;
    background-color: gray;
    list-style-type: none;
    margin: 0;
    padding: 0;
}

.eventTrackerBox {
    width: 100%;
    height: 10%;
    -webkit-transition: all 0.2s ease-out;
    -o-transition: all 0.2s ease-out;
    transition: all 0.2s ease-out;
}

.eventTrackerItem {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: start;
        justify-content: flex-start;
    width: 100%;
    height: 100%;
    border: 1px solid black;
}

.example-enter {
    -webkit-transition: all 0.5s ease-in;
    -o-transition: all 0.5s ease-in;
    transition: all 0.5s ease-in;
    -webkit-transform: translateY(120%);
        -ms-transform: translateY(120%);
            transform: translateY(120%);
    /* max-height: 0; */
}

.example-enter.example-enter-active {
    -webkit-transform: translateY(0);
        -ms-transform: translateY(0);
            transform: translateY(0);
    /* max-height: 100px; */
}

.example-leave {
    /* position: absolute;
    left: 0;
    bottom: 0; */
    /* max-height: 100px; */
    -webkit-transition: all 0.5s ease-out;
    -o-transition: all 0.5s ease-out;
    transition: all 0.5s ease-out;
    -webkit-transform: translateY(0);
        -ms-transform: translateY(0);
            transform: translateY(0);
}

.example-leave.example-leave-active {
    /* max-height: 0; */
    -webkit-transform: translateY(-100%);
        -ms-transform: translateY(-100%);
            transform: translateY(-100%);
}

.eventTrackerItem__TS {
    -ms-flex-align: center;
        align-items: center;
    padding-left: 20px;
    width: 5%;
}

.eventTrackerItem__ID {
    font-weight: bold;
    width: 5%;
}

.eventTrackerItem__ev {
    width: 10%;
}

.eventTrackerItem__city {
    width: 20%;
}

.tryit {
    position: fixed;
    left: 0px;
    top: 0px;
}

@-webkit-keyframes popupAndHide {
    0% {
        opacity: 1;
        r: 0%;
    }
    50% {
        opacity: 1;
        r: 1%;
    }
    100% {
        opacity: 0;
    }
}

@keyframes popupAndHide {
    0% {
        opacity: 1;
        r: 0%;
    }
    50% {
        opacity: 1;
        r: 1%;
    }
    100% {
        opacity: 0;
    }
}


